.coach-selection-container {
  padding: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.coach-selection-title {
  text-align: center;
  font-size: 2.5rem;
  color: #fff;
  margin-bottom: 0.5rem;
  font-weight: 700;
}

.coach-selection-subtitle {
  text-align: center;
  color: #aaa;
  font-size: 1.1rem;
  margin-bottom: 3rem;
}

.coach-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 2rem;
}

.coach-card {
  background: #1a1a1a;
  border-radius: 15px;
  overflow: hidden;
  cursor: pointer;
  transition: all 0.3s ease;
  border: 2px solid transparent;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
}

.coach-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.5);
}

.coach-card.selected {
  border-width: 3px;
  transform: scale(1.02);
}

.coach-card-header {
  padding: 1.5rem;
  text-align: center;
}

.coach-card-header h3 {
  margin: 0;
  font-size: 1.8rem;
  color: #fff;
  font-weight: 700;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.coach-card-body {
  padding: 1.5rem;
}

.coach-description {
  color: #ccc;
  font-size: 1rem;
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

.coach-specialties {
  margin-bottom: 1.5rem;
}

.coach-specialties h4 {
  color: #fff;
  font-size: 0.9rem;
  margin-bottom: 0.5rem;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.specialty-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.specialty-tag {
  padding: 0.3rem 0.8rem;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 500;
}

.coach-personality {
  text-align: center;
  margin-bottom: 1rem;
}

.personality-badge {
  font-size: 0.9rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.coach-card-footer {
  padding: 0 1.5rem 1.5rem;
}

.select-coach-btn {
  width: 100%;
  padding: 0.8rem;
  border-radius: 8px;
  border: 2px solid;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.select-coach-btn:hover {
  transform: scale(1.05);
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .coach-selection-title {
    font-size: 2rem;
  }
  
  .coach-cards {
    grid-template-columns: 1fr;
  }
}
