/* Modern Dating Hub Styles */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.dating-hub {
  min-height: 100vh;
  background: #f8f9fa;
  font-family: 'Poppins', sans-serif;
}

/* Hero Section */
.hub-hero {
  background: linear-gradient(135deg, #ff6b6b 0%, #4ecdc4 100%);
  padding: 80px 20px;
  text-align: center;
  position: relative;
  overflow: hidden;
}

.hero-content {
  max-width: 800px;
  margin: 0 auto;
  position: relative;
  z-index: 2;
}

.hero-title {
  font-size: 3.5rem;
  font-weight: 700;
  color: white;
  margin-bottom: 20px;
  line-height: 1.2;
  position: relative;
}

.title-emoji {
  display: inline-block;
  animation: pulse 2s ease-in-out infinite;
  margin-left: 10px;
}

@keyframes pulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.1); }
}

.hero-subtitle {
  font-size: 1.25rem;
  color: rgba(255, 255, 255, 0.9);
  font-weight: 300;
  max-width: 600px;
  margin: 0 auto;
}

.hero-decoration {
  position: absolute;
  top: -50%;
  right: -20%;
  width: 600px;
  height: 600px;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.1) 0%, transparent 70%);
  animation: float 20s ease-in-out infinite;
}

@keyframes float {
  0%, 100% { transform: translateY(0) rotate(0deg); }
  50% { transform: translateY(-30px) rotate(180deg); }
}

/* Matches Section */
.matches-section {
  padding: 60px 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.section-header {
  text-align: center;
  margin-bottom: 50px;
}

.section-header h2 {
  font-size: 2.5rem;
  color: #2d3436;
  margin-bottom: 10px;
}

.section-header p {
  font-size: 1.1rem;
  color: #636e72;
}

/* Matches Grid */
.matches-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 30px;
}

.match-card {
  background: white;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.08);
  transition: all 0.3s ease;
  position: relative;
}

.match-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.12);
}

.match-image {
  position: relative;
  height: 200px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

.avatar-gradient {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.avatar-gradient[data-initial="S"] { background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%); }
.avatar-gradient[data-initial="E"] { background: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%); }
.avatar-gradient[data-initial="A"] { background: linear-gradient(135deg, #fa709a 0%, #fee140 100%); }
.avatar-gradient[data-initial="M"] { background: linear-gradient(135deg, #30cfd0 0%, #330867 100%); }

.avatar-letter {
  font-size: 4rem;
  font-weight: 700;
  color: white;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.2);
}

.online-indicator {
  position: absolute;
  bottom: 15px;
  right: 15px;
  width: 15px;
  height: 15px;
  background: #2ecc71;
  border: 3px solid white;
  border-radius: 50%;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

.match-info {
  padding: 25px;
}

.match-name {
  font-size: 1.5rem;
  color: #2d3436;
  margin-bottom: 5px;
}

.match-occupation {
  font-size: 1rem;
  color: #74b9ff;
  margin-bottom: 10px;
  font-weight: 500;
}

.match-personality {
  font-size: 0.95rem;
  color: #636e72;
  margin-bottom: 15px;
  line-height: 1.5;
}

.match-interests {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-bottom: 20px;
}

.interest-pill {
  background: #f1f3f5;
  color: #495057;
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 500;
}

.interest-more {
  background: #e9ecef;
  color: #868e96;
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 500;
}

.start-chat-btn {
  width: calc(100% - 50px);
  margin: 0 25px 25px;
  padding: 14px 24px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.start-chat-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(102, 126, 234, 0.4);
}

.btn-icon {
  transition: transform 0.3s ease;
}

.start-chat-btn:hover .btn-icon {
  transform: translateX(3px);
}

/* Features Section */
.features-section {
  background: #f1f3f5;
  padding: 80px 20px;
  text-align: center;
}

.features-section h2 {
  font-size: 2.5rem;
  color: #2d3436;
  margin-bottom: 50px;
}

.features-grid {
  max-width: 1000px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 30px;
}

.feature-card {
  background: white;
  padding: 30px;
  border-radius: 16px;
  transition: transform 0.3s ease;
}

.feature-card:hover {
  transform: translateY(-5px);
}

.feature-icon {
  font-size: 3rem;
  margin-bottom: 20px;
}

.feature-card h3 {
  font-size: 1.25rem;
  color: #2d3436;
  margin-bottom: 10px;
}

.feature-card p {
  font-size: 0.95rem;
  color: #636e72;
  line-height: 1.6;
}

/* Back Button */
.simulation-wrapper {
  min-height: 100vh;
  background: #f8f9fa;
}

.back-button {
  position: fixed;
  top: 20px;
  left: 20px;
  background: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(0, 0, 0, 0.1);
  padding: 10px 20px;
  border-radius: 25px;
  font-size: 0.95rem;
  color: #2d3436;
  cursor: pointer;
  transition: all 0.3s ease;
  z-index: 100;
  font-family: 'Poppins', sans-serif;
}

.back-button:hover {
  background: white;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

/* Responsive Design */
@media (max-width: 768px) {
  .hero-title {
    font-size: 2.5rem;
  }
  
  .hero-subtitle {
    font-size: 1.1rem;
  }
  
  .matches-grid {
    grid-template-columns: 1fr;
  }
  
  .features-grid {
    grid-template-columns: 1fr;
  }
}

/* Immersive Dating Simulation Styles */
.immersive-date-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: #000;
  z-index: 1000;
  overflow: hidden;
}

.date-main-view {
  width: 100%;
  height: 100%;
  position: relative;
}

/* Date Info Overlay - Top Center */
.date-info-overlay {
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(10px);
  padding: 15px 30px;
  border-radius: 30px;
  text-align: center;
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.date-info-overlay h3 {
  margin: 0 0 5px 0;
  font-size: 18px;
  font-weight: 600;
}

.date-info-overlay .date-status {
  margin: 0;
  font-size: 14px;
  color: #0ff;
  animation: pulse 2s ease-in-out infinite;
}

/* User PiP Container - Bottom Right */
.user-pip-container {
  position: absolute;
  bottom: 100px;
  right: 20px;
  width: 200px;
  height: 150px;
  background: #000;
  border-radius: 15px;
  overflow: hidden;
  border: 2px solid rgba(255, 255, 255, 0.2);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.5);
}

/* Date Controls - Bottom Center */
.date-controls {
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 15px;
  padding: 10px;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(10px);
  border-radius: 25px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.control-button {
  padding: 12px 24px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 20px;
  color: white;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 8px;
}

.control-button:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateY(-2px);
}

.control-button.active {
  background: rgba(255, 0, 0, 0.5);
  border-color: #ff0000;
  animation: recording 1.5s ease-in-out infinite;
}

.control-button.end-date {
  background: rgba(255, 0, 0, 0.2);
  border-color: rgba(255, 0, 0, 0.5);
}

.control-button.end-date:hover {
  background: rgba(255, 0, 0, 0.4);
}

/* Connection Status */
.connection-status {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgba(0, 0, 0, 0.9);
  padding: 20px 40px;
  border-radius: 20px;
  color: white;
  display: flex;
  align-items: center;
  gap: 15px;
  backdrop-filter: blur(10px);
}

.status-dot {
  width: 10px;
  height: 10px;
  background: #0ff;
  border-radius: 50%;
  animation: pulse 1s ease-in-out infinite;
}

/* Animations */
@keyframes pulse {
  0%, 100% { opacity: 0.6; }
  50% { opacity: 1; }
}

@keyframes recording {
  0%, 100% { opacity: 0.8; }
  50% { opacity: 1; box-shadow: 0 0 20px rgba(255, 0, 0, 0.5); }
}

/* Remove old styles */
.date-controls-overlay,
.connection-status-overlay,
.emotion-insights {
  display: none;
}
