.simplified-home {
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
}

.hero-section {
  max-width: 1200px;
  width: 100%;
  text-align: center;
}

.app-title {
  font-size: 4rem;
  color: white;
  margin-bottom: 0.5rem;
  font-weight: bold;
  text-shadow: 2px 2px 4px rgba(0,0,0,0.2);
}

.tagline {
  font-size: 1.5rem;
  color: rgba(255,255,255,0.9);
  margin-bottom: 3rem;
}

.core-features {
  margin-bottom: 2rem;
}

.feature-card {
  background: rgba(255,255,255,0.95);
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 10px 30px rgba(0,0,0,0.2);
  transition: transform 0.3s ease;
}

.feature-card:hover {
  transform: translateY(-5px);
}

.feature-card.main {
  max-width: 600px;
  margin: 0 auto 2rem;
}

.feature-card h2 {
  font-size: 2rem;
  color: #764ba2;
  margin-bottom: 1rem;
}

.feature-card h3 {
  font-size: 1.3rem;
  color: #667eea;
  margin-bottom: 0.5rem;
}

.feature-card p {
  color: #666;
  margin-bottom: 1.5rem;
  line-height: 1.6;
}

.cta-button {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  padding: 1rem 3rem;
  font-size: 1.2rem;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: bold;
}

.cta-button:hover {
  transform: scale(1.05);
  box-shadow: 0 5px 20px rgba(0,0,0,0.3);
}

.feature-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
  max-width: 1000px;
  margin: 0 auto;
}

.feature-grid .feature-card {
  padding: 1.5rem;
}

.feature-grid button {
  background: white;
  color: #667eea;
  border: 2px solid #667eea;
  padding: 0.7rem 1.5rem;
  border-radius: 25px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 600;
}

.feature-grid button:hover {
  background: #667eea;
  color: white;
}

.quick-links {
  margin-top: 3rem;
  display: flex;
  gap: 1rem;
  justify-content: center;
}

.secondary-button {
  background: transparent;
  color: white;
  border: 2px solid white;
  padding: 0.7rem 1.5rem;
  border-radius: 25px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 600;
}

.secondary-button:hover {
  background: white;
  color: #764ba2;
}
